body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(11, 11, 11, 0.5)), to(rgba(11, 11, 11, 0.5))), url("https://images.unsplash.com/photo-1549212348-813047bf0363?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=700&q=80");
  background: -webkit-linear-gradient(bottom, rgba(11, 11, 11, 0.5), rgba(11, 11, 11, 0.5)), url("https://images.unsplash.com/photo-1549212348-813047bf0363?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=700&q=80");
  background: linear-gradient(0deg, rgba(11, 11, 11, 0.5), rgba(11, 11, 11, 0.5)), url("https://images.unsplash.com/photo-1549212348-813047bf0363?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=700&q=80");
  background-size: cover;
  height: 100vh;
  color: #ffffff;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }
  .header__top {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    padding: 3.2rem 2rem;
    margin: 0 10rem; }
    @media screen and (max-width: 960px) {
      .header__top {
        margin: 0 0; } }
  .header__arrow {
    position: absolute;
    bottom: 5%; }
  .header__nav ul {
    display: flex;
    justify-content: space-between; }
  .header__option {
    display: block;
    margin-right: 2rem;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 300;
    letter-spacing: 2px; }
  .header__text {
    text-align: center;
    /*
    position: absolute;
    left: 50%;
    margin-left: -50px;
    top: 50%;
    margin-top: -50px;
    text-align: center;*/ }
  .header__text--title {
    text-transform: uppercase;
    font-size: 5.5rem;
    letter-spacing: 15px;
    font-weight: 400; }
  .header__text--subtitle {
    text-transform: uppercase;
    font-weight: 300; }

.collection__container {
  margin-right: auto;
  margin-left: auto;
  max-width: 960px;
  padding: 5rem 10px;
  text-align: center; }

.collection__subtitle {
  text-transform: uppercase;
  color: #8a8585;
  font-weight: 600; }

.collection__title {
  text-transform: uppercase;
  font-size: 2.2rem;
  font-family: "Roboto Condensed", sans-serif;
  letter-spacing: 5px; }

.collection__categories__option {
  color: #8a8585;
  display: inline-block;
  margin-top: 2rem;
  list-style: none;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 700;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  margin-right: 2.4rem;
  border-bottom: 2px solid transparent; }
  .collection__categories__option:nth-last-child(1) {
    margin-right: 0px; }
  .collection__categories__option:hover {
    border-bottom: 2px solid #8a8585; }

.collection__gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas: "large-1   large-2   small-1" "large-1   large-2   large-3" "small-2   large-4   large-3" "large-5   large-4   large-6" "large-5   small-3   large-6";
  grid-gap: 10px;
  padding: 1rem;
  width: 90%;
  margin: 0 auto; }
  .collection__gallery__item {
    display: block;
    position: relative;
    min-height: 200px;
    height: 100%; }
    .collection__gallery__item:nth-child(1) {
      grid-row-start: large-1;
      grid-row-end: large-1; }
    .collection__gallery__item:nth-child(2) {
      grid-row-start: large-2;
      grid-row-end: large-2; }
    .collection__gallery__item:nth-child(3) {
      grid-row-start: small-1;
      grid-row-end: small-1; }
    .collection__gallery__item:nth-child(4) {
      grid-row-start: small-2;
      grid-row-end: small-2; }
    .collection__gallery__item:nth-child(5) {
      grid-row-start: large-4;
      grid-row-end: large-4; }
    .collection__gallery__item:nth-child(6) {
      grid-row-start: large-3;
      grid-row-end: large-3; }
    .collection__gallery__item:nth-child(7) {
      grid-row-start: large-5;
      grid-row-end: large-5; }
    .collection__gallery__item:nth-child(8) {
      grid-row-start: small-3;
      grid-row-end: small-3; }
    .collection__gallery__item:nth-child(9) {
      grid-row-start: large-6;
      grid-row-end: large-6; }
    .collection__gallery__item:hover .collection__gallery__description {
      background: -webkit-gradient(linear, left bottom, left top, from(rgba(138, 133, 133, 0.6)), to(rgba(138, 133, 133, 0.6)));
      background: -webkit-linear-gradient(bottom, rgba(138, 133, 133, 0.6), rgba(138, 133, 133, 0.6));
      background: linear-gradient(0deg, rgba(138, 133, 133, 0.6), rgba(138, 133, 133, 0.6)); }
    .collection__gallery__item:hover .collection__gallery__description {
      display: flex; }
  .collection__gallery__img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
    -webkit-transition: 0.5s;
    transition: 0.5s; }
  .collection__gallery__description {
    position: absolute;
    top: 0;
    height: 100%;
    color: #ffffff;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .collection__gallery__title {
    margin-right: 1.2rem;
    margin-left: 1.2rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-weight: 300;
    font-size: 1.5rem;
    position: relative; }
    .collection__gallery__title:after {
      position: absolute;
      bottom: 0;
      right: 0;
      bottom: -10%;
      content: "";
      height: 10px;
      width: 100%;
      border-bottom: 1px solid #ffffff; }
  .collection__gallery__subcontent {
    font-size: 0.8rem;
    margin: 0 2rem; }
  .collection__gallery .button {
    position: absolute;
    bottom: 10%; }


.button {
  color: #8a8585;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 3px;
  padding: 1rem;
  cursor: pointer;
  border: 2px solid #8a8585;
  font-size: 0.9rem;
  -webkit-transition: 0.6s ease;
  transition: 0.6s ease; }

.button-no-bg {
  background: none; }
  .button-no-bg:hover {
    background: #8a8585;
    color: #ffffff; }

.button-no-border {
  background: none;
  border: none; }

.button-white {
  color: #ffffff; }

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #1b1a1a;
}

a {
  color: #8a8585;
  text-decoration: none;
}

